import React from 'react';

const CustomInputRange = ({
  containerID,
  label,
  onClickShow,
  symbol,
  onChangeMin,
  onChangeMax,
  minValue,
  maxValue,
}) => {
  return (
    <div className="min-max-wrapper form-group col-12 col-sm-12 col-md">
      <div onClick={() => onClickShow()} className="show-trigger">
        <label>{label}</label>
        <select id="unit-size"></select>
      </div>
      <div
        id={containerID}
        style={{
          display: 'none',
        }}
        className="col-12 col-md-12 col-md min-max-container"
      >
        <div className="fields-wrapper">
          <div className="col-6 col-sm-6 col-sm">
            <label>Min</label>
            <div className="input-group mb-3">
              <input
                type="number"
                onChange={(event) => onChangeMin(event.target.value)}
                className="form-control"
                id="minUnitSize"
                value={minValue}
              />
              <span className="input-group-text">{symbol}</span>
            </div>
          </div>
          <span
            style={{
              alignSelf: 'center',
            }}
          >
            to
          </span>
          <div className="col-6 col-sm-6 col-sm">
            <label>Max</label>
            <div className="input-group mb-3">
              <input
                type="number"
                onChange={(event) => onChangeMax(event.target.value)}
                className="form-control"
                id="maxUnitSize"
                value={maxValue}
              />
              <span className="input-group-text">{symbol}</span>
            </div>
          </div>
        </div>
        <button type="button" id="unit-size-btn">
          Done
        </button>
      </div>
    </div>
  );
};

export default CustomInputRange;
