import axios from 'axios';
import { store } from '../store';

const getURL = () => {
  const config = store.getState().configuration;
  return config.apiUrl;
};

export const getFloorPlans = async (buildingId, withMedia, page, recordsPerPage, xPmc, requestBody) => {
  const experienceApiUrl = getURL();
  try {
    const res = await axios({
      url: `${experienceApiUrl}/floor_plan/search/1?withMedia=true&page=1&recordsPerPage=20`,
      method: 'post',
    });
    const floorPlans = res.data || null;
    return floorPlans;
  } catch (error) {
    return error;
  }
};
