import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { getFloorPlans } from '../services/floorPlan';

const initialSearchResultState = {
  floorPlans: {},
};

export const searchFloorPlans = createAsyncThunk(
  'searchResult/searchFloorPlans',
  async (filters, thunkAPI) => {
    const response = await getFloorPlans(1);
    return response.responseBody;
  }
);

const searchResultSlice = createSlice({
  name: 'searchResult',
  initialState: initialSearchResultState,
  extraReducers: {
    [searchFloorPlans.pending]: (state) => {
      state.floorPlans = { status: 'pending' };
    },
    [searchFloorPlans.fulfilled]: (state, { payload }) => {
      state.floorPlans = payload;
    },
    [searchFloorPlans.rejected]: (state) => {
      state.floorPlans = { status: 'rejected' };
    },
  },
});

export default searchResultSlice.reducer;
