import React, { useEffect, useState } from "react";
import { useSelector } from 'react-redux';
import { format, parseISO } from 'date-fns';
import "../App.css";
import "../components/Apartment/apartment.css";
import ApartmentHeader from "../components/Apartment/ApartmentHeader";
import ApartmentFilter from "../components/Apartment/ApartmentFilter";
import ApartmentList from "../components/Apartment/ApartmentList";
// import PropTypes from "prop-types";

import Query from '../services/utils/Query';
import { loadUnits, loadUnitPricing } from '../services/ApartmentQuery/apartmentQueries';

function Apartment(props) {
  // Get buildingId in store
  // Save responseBody in store after the API Call

  const configuration = useSelector((state) => { return state.configuration; });
  console.log("CONFIG: ", JSON.stringify(configuration));
  const selection = useSelector((state) => { return state.userSelection; });
  console.log("SELECTION: ", JSON.stringify(selection));
  const filters = useSelector((state) => { return state.searchFilter; });
  console.log("FILTERS:", JSON.stringify(filters));

  // initial value
  const buildingId = configuration.buildingId ? configuration.buildingId : '1'; //selection.floorPlan.buildingId;
  const floorPlanId = selection.floorPlan.id ? selection.floorPlan.id : '1';
  console.log("buildingId: " + buildingId);
  console.log("floorPlanId: " + floorPlanId);

  const [apartments, setApartments] = useState([]);
  const [apartmentNumber, setApartmentNumber] = useState(filters.apartmentNumber !== '' ? filters.apartmentNumber : '');
  const [moveInDate, setMoveInDate] = useState(filters.moveInDate !== '' ? filters.moveInDate : format(new Date(), "yyyy-MM-dd"));
  const [apartmentResults, setApartmentResults] = useState([]);
  const [pricingResults, setPricingResults] = useState([]);
  const [loading, setLoading] = useState(false);

  // Simplify passing of state variables to Apartment Filter Component props
  const filterProps = { apartmentNumber, setApartmentNumber, moveInDate, setMoveInDate };

  const getApartments = async() => {
    setLoading(true);
    // let query = new Query();
    // query.and('unitNumber').match(apartmentNumber);
    // query.and('floorPlanId').eq(floorPlanId);

    try {
      let { records } = await loadUnits(floorPlanId);
      if (apartments.length === 0) setApartments(records); // TODO get from redux store

      // temporary, to be romoved if unit filter in API is working properly
      let data = records.filter((unit) => {
          return unit.unitNumber.toLowerCase().includes(apartmentNumber.toLowerCase())
      });

      setApartmentResults(data);
      
      console.log("Apartments:", records);
    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  }

  const getUnitPricing = async() => {
    try {
      setLoading(true);
      const moveDate = format(parseISO(moveInDate), "yyyy-MM-dd'T'HH:mm:ss.SSS+0000"); // +0000 instead of XX getting actual timezone
      console.log("Move-in Date: "+moveDate);

      let query = new Query();
      query.and('floorPlanId').eq(floorPlanId);
      query.and('startDate').lessThanOrEq(moveDate);
      query.and('endDate').greaterThanOrEq(moveDate);
    
      let { records } = await loadUnitPricing(query);
      console.log("UnitPricing: ", records);

      const unitPricing = records;
      if (apartmentNumber !== '') {
        let unitIds = apartments.reduce((result, unit) => {
          if (unit.unitNumber.toLowerCase().includes(apartmentNumber.toLowerCase())) {
            result.push(unit.id);
          }
          return result;
        }, []);
        console.log("UnitIds:", unitIds);
        const filteredPricing = unitPricing.filter((item) => {
          return unitIds.includes(item.unitId)
        });
        console.log(filteredPricing);
        setPricingResults(filteredPricing);
      } else {
        setPricingResults(unitPricing);
      }

      const filtered = apartments.filter((apartment) => 
          pricingResults.some(pricing => {
            return pricing.unitId === apartment.id;
          })
      );
      setApartmentResults(filtered);
      console.log("Filtered: ",filtered);

    } catch (error) {
      console.error(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  };

  // init onload
  useEffect(() => {
    //if (filters.moveInDate !== '') setMoveInDate(filters.moveInDate);
    //if (filters.apartmentNumber !== '') setApartmentNumber(filters.apartmentNumber);

    console.log("Move-In Date Init: "+ moveInDate);
    console.log("Apartment Number Init: "+ apartmentNumber);

    const delayDebounceFn = setTimeout(() => {
      getApartments();
      //getUnitPricing();
    }, 1000);
    return () => clearTimeout(delayDebounceFn)
  }, [moveInDate, apartmentNumber]);

  const NoDataFound = () => {
      return <p>No results found.</p>
  }

  const Spinner = () => {
    return <div className="loader"></div>
  }

  return (
    <div className="Apartment">
      <ApartmentHeader floorplan={selection.floorPlan} />
      <ApartmentFilter filterProps={filterProps} />
      { !loading ?
        apartmentResults && apartmentResults.length > 0 ? 
        <ApartmentList apartments={apartmentResults} floorplan={selection.floorPlan} />
        : <NoDataFound />
      : <Spinner /> }
    </div>
  );
}

// Apartment.propTypes = {
//   buildingId: PropTypes.string,
//   floorPlanId: PropTypes.string
// }

// Apartment.defaultProps = {
//   buildingId: "1",
//   floorPlanId: "1"
// }

export default Apartment;
