import React from 'react'

function AparmentCards({data}) {
  return (
      <>
      {data.map((item,i) => {
          const {joins} = item;
          const joined = new Array(joins);


          return (
<>
<p>
               {joined[0]['u.name']} | {joined[0]['u.bedrooms']} Bedroom | {joined[0]['u.bathrooms']} Bathrooms | {joined[0]['u.livingArea']} {joined[0]['u.livingAreaMeasure']}
                </p>
 <div  className="container" style={{marginBottom:10}}>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Apartment</div>
              <div className="col-xs-push-1 detail">{item.unitNumber}</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Sq. Ft.</div>
              <div className="col-xs-push-1 detail"> {joined[0]['u.livingArea']} {joined[0]['u.livingAreaMeasure']}</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Price per Month</div>
              <div className="col-xs-push-1 detail">${item.minPrice} - ${item.maxPrice}*</div>
            </div>
            <div className="col-xs-2"></div>
            <div className="col-xs-4 button"><button className="gold">Select</button></div>
          </div>
               </>
          )
     
      })}
  
          </>
  )
}

export default AparmentCards