import React from 'react';
//import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RouteTypes } from '../../constants/Routes/RouteTypes';

const ApartmentList = ({ apartments, floorplan }) => {

  const navigate = useNavigate();
  //const dispatch = useDispatch();
  
  return (
    <div className="row">
      {
        apartments.map(apartment =>
          <div key={apartment.id} className="container">
            <div className="col-xs-2">
              <div className="col-xs-push-1">Apartment</div>
              <div className="col-xs-push-1 detail">{apartment.unitNumber}</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Sq. Ft.</div>
              <div className="col-xs-push-1 detail">{apartment["floorPlan.livingArea"]} Sq. Ft.</div>
            </div>
            <div className="col-xs-2">
              <div className="col-xs-push-1">Price per Month</div>
              <div className="col-xs-push-1 detail">${apartment.minPrice} - ${apartment.maxPrice}*</div>
            </div>
            <div className="col-xs-2"></div>
            <div className="col-xs-4 button">
              <button className="gold target"
                button="primary"
                onClick={async(e) => {
                  //await dispatch(setApartments(apartments));
                  await navigate(RouteTypes.PRICING);
                }}>
              Select
              </button>
            </div>
          </div>
        )
      }
    </div>
  );
}

export default ApartmentList;